@use '@miq/styles/src/variables' as *;
@use '@miq/styles/src/mixins' as *;

.lib-createview {
}
.lib-createview .cat-grid {
  padding-bottom: 0;
  grid-auto-columns: max-content;
  gap: 6px;

  .item {
    padding: 0.4rem 0.75rem;
    background-color: #f7f8fc;
    border-radius: var(--border-radius);
  }
}

.lib-createview .lib-createview-header {
  // background-color: white;
}

.lib-createview .lib-createview-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f7f8fc;

  a {
    display: block;
    padding: 0.5rem 0.75rem;
    border: 1px solid var(--blue-600);
    border-radius: var(--border-radius);
    background-color: var(--blue-100);
    margin-bottom: 12px;
    min-width: 176px;

    text-align: center;
    font-weight: 500;
  }
}

.order-item-form .w-submit {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  width: 100%;
  background-color: #25d366;
  border-color: #25d3655b;
  color: white;
}

.miq-shop-product-detail-view-grid-product h1 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}
.miq-shop-product-detail-view-grid-product .p-price {
  margin-bottom: 0.5rem;
}

.miq-shop-product-detail-view-grid-product .p-price-sale,
.miq-shop-product-detail-view-grid-product .p-price-retail {
  font-size: 1.25rem;
}
